body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-background: #feffd8; /* Lightest */
  --color-secondary-background: #dde7c7;
  --color-card: #bfd8bd;
  --color-primary: #98c9a3; /* Buttons and accents */
  --color-secondary: #77bfa3; /* Borders and highlights */
  --color-text: #333; /* Default text */
  --color-text-muted: #555; /* Secondary text */
}

/* Global Reset */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: var(--color-background);
  color: var(--color-text);
}