/* General Styling */
.home-page {
    font-family: Arial, sans-serif;
    color: var(--color-text);
    padding: 20px;
}

/* Hero Section */
.hero {
    text-align: center;
    padding: 100px 20px;
    background-image: url('./sunmoon.png');
    background-size: cover;
    background-position: center;
    color: white;
    box-shadow: inset 0 0 200px rgba(0, 0, 0, 0.5);
}

.hero-title {
    font-size: 3.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.7);
}

.hero-subtitle {
    font-size: 1.5rem;
    margin-bottom: 30px;
}

.cta-button {
    display: inline-block;
    padding: 15px 30px;
    font-size: 1.2rem;
    color: white;
    background-color: var(--color-secondary);
    border: none;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s ease;
}

.cta-button:hover {
    background-color: var(--color-primary);
}

/* Highlights Section */
.highlights {
    text-align: center;
    padding: 50px 20px;
    background-color: var(--color-background-alt);
}

.highlights h2 {
    font-size: 2rem;
    margin-bottom: 30px;
    color: var(--color-primary);
}

.highlights-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

.highlight-card {
    text-align: center;
    padding: 20px;
    width: 250px;
    background-color: var(--color-background);
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    text-decoration: none;
    color: var(--color-text);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.highlight-card:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.highlight-card h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: var(--color-primary);
}

.highlight-card p {
    font-size: 1rem;
}

/* Footer */
.home-footer {
    text-align: center;
    margin-top: 50px;
    font-size: 0.9rem;
    color: var(--color-text-muted);
}