/* About Me Page */
.about-me {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    padding: 20px;
    background-color: #fff;
    line-height: 1.6;
    margin-bottom: 100px;
}

/* Portrait Wrapper */
.portrait-wrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 20px;
}

/* Portrait Section */
.portrait {
    border-radius: 50%;
    width: 250px;
    height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 5px solid var(--color-secondary);
    overflow: hidden;
}

.portrait img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.highlighted-name {
    font-weight: bold;
    color: #007BFF;
    font-size: 1.2em;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

/* Bio and Info Sections */
.info {
    flex: 2;
    display: flex;
    flex-direction: column;
}

.bio h1 {
    font-size: 2.5rem;
    color: var(--color-primary);
}

.bio p {
    font-size: 1.2rem;
    color: var(--color-text-muted);
}

.quick-facts h2 {
    font-size: 1.5rem;
    color: var(--color-primary);
}

.quick-facts ul {
    list-style-type: none;
    padding: 0;
}

.quick-facts li {
    font-size: 1.1rem;
    margin-bottom: 8px;
}

.quick-facts li::before {
    content: '✔️';
    margin-right: 10px;
    color: var(--color-secondary);
}
