/* Gallery Grid */
.gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 50px;
    padding: 30px; 
    padding-bottom: 80px;
    justify-content: center;
}

/* Project Card */
.project-card {
    text-align: center;
    color: var(--color-text);
    transition: transform 0.3s ease;
}

/* Hover Effect for Project Card */
.project-card:hover {
    transform: scale(1.1);
}

/* Thumbnail Title */
.project-title {
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--color-primary);
    margin-bottom: 15px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
    letter-spacing: 0.5px; 
    text-align: center; 
    transition: color 0.3s ease; 
}

/* Hover Effect for Thumbnail Title */
.project-title:hover {
    color: var(--color-secondary); 
}

/* Projects Page Title */
.projects-page h1 {
    font-size: 3rem;
    font-weight: bold;
    color: var(--color-primary);
    text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;
    padding: 15px 40px;
    display: inline-block;
    border: 4px solid var(--color-secondary);
    border-radius: 12px;
    background-color: var(--color-background);
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    position: relative;
    left: 60px;
    text-align: center;
}

/* Image Container */
.image-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 56.25%;
    overflow: hidden; 
    border: 4px solid var(--color-secondary);
    border-radius: 8px; 
    transition: box-shadow 0.3s ease, border-color 0.3s ease; 
}

.image-container:hover {
    border-color: var(--color-primary); 
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

/* Static Thumbnail: Display by default */
.static-thumbnail {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; 
    display: block; 
    border-radius: 8px;
}

/* Animated Thumbnail: Display only on hover */
.animated-thumbnail {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; 
    display: none; 
    border-radius: 8px; 
}

/* Hover State: Show GIF, Hide Static Thumbnail */
.image-container:hover .static-thumbnail {
    display: none;
}

.image-container:hover .animated-thumbnail {
    display: block;
}

/* Hover Text */
.hover-text {
    position: absolute;
    bottom: 10px;
    right: 10px;
    background-color: rgba(255, 88, 28, 0.6);
    color: white;
    padding: 5px 10px;
    font-size: 0.9rem;
    border-radius: 5px;
    display: none;
}

.image-container:hover .hover-text {
    display: block;
}

.project-card a {
    text-decoration: none;
    color: inherit;
}

.project-card a:focus-visible {
    outline: 2px solid var(--color-primary);
    outline-offset: 4px;
}
