/* Overall Project Detail Layout */
.project-detail {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px;
    font-family: Arial, sans-serif;
    color: var(--color-text);
}

/* Left Side: Media Section */
.project-media {
    flex: 1 1 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.project-gif {
    max-width: 100%;
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

/* Right Side: Info Section */
.project-info {
    flex: 1 1 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 15px;
}

/* Project Title */
.project-title {
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--color-primary);
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
}

/* Tools Section */
.project-tools {
    font-size: 1.1rem;
    color: var(--color-accent);
    margin-bottom: -20px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
    padding: 0 60px;
}

/* Project Description */
.project-description {
    font-size: 1.2rem;
    line-height: 1.6;
    color: var(--color-text);
    padding: 0 60px;
}

/* Back Button */
.back-button {
    align-self: flex-start;
    padding: 10px 20px;
    font-size: 1rem;
    background-color: var(--color-secondary);
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.back-button:hover {
    background-color: var(--color-primary);
}